.hidden {
  display: none;
}

.icon-wrapper {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0,0,0,0);
    border-radius: 0;
}

.icon-inner {
  position: absolute;
  left: 85%;
  top: 85%;
  width: 80px;
  height: 80px;
  -o-object-fit: cover;
     object-fit: cover;
  margin: auto;
  background: rgba(0,0,0,0);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.icon-content {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.icon-img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  margin: 0 auto;
  -webkit-box-shadow: 0px 3px 3px 1px rgba(0,0,0,0.3);
          box-shadow: 0px 3px 3px 1px rgba(0,0,0,0.3)
}



